<template>
	<div id="finance-getcash">
		<public-header title="申请提现"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">
						账户可用余额:
						<span class="c-info margin-5-l">¥{{account.amount}}</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t padding-15 border-0px">
				<p class="font-14 margin-10-b">提现金额</p>
				<input v-model.number="get_fee" type="text" class="getcash-num" placeholder="请输入提现金额">
				<div class="getcash-rule">
					<p>最低提现金额为{{account.fee_limit}}元</p>
					<p>提现费率为{{account.fee_rate}}%，最低收取{{account.fee_min}}元，最高收取{{account.fee_max}}元</p>
				</div>
			</van-cell-group>
			<van-radio-group v-model="channel" v-if="config && config.type">
				<p class="getcash-type">提现方式</p>
				<van-cell-group class="border-0px">
					<van-cell title="微信提现" clickable @click="channel = 'weixin'">
						<van-radio name="weixin" />
					</van-cell>
					<van-cell title="支付宝提现" clickable @click="channel = 'alipay'" v-if="config.type.alipay == 1">
						<van-radio name="alipay" />
					</van-cell>
					<van-cell title="银行卡提现" clickable @click="channel = 'bank'" v-if="config.type.bank == 1">
						<van-radio name="bank" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" :disabled="!islegal" block class="font-16 bg-info" @click="onSubmit">提现</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				channel: 'weixin',
				account: {},
				get_fee: '',
				showPreLoading: true,
				islegal: false,
				config: {}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/finance/getcash/index',
					autoAssign: true,
					variable: 'account',
					success: function(res) {
						that.islegal = true;
						that.config = res.config;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				let get_fee = parseFloat(that.get_fee);
				if(isNaN(get_fee) || get_fee <= 0) {
					that.util.$toast('提现金额有误');
					return false;
				}
				if(get_fee > that.account.amount) {
					that.util.$toast('提现金额不能大于账户可用余额');
					return false;
				}
				that.islegal = false;
				var rule_fee = (get_fee * that.account.fee_rate/100).toFixed(2);
				rule_fee = Math.max(rule_fee, that.account.fee_min);
				if(that.account.fee_max > 0) {
					rule_fee = Math.min(rule_fee, that.account.fee_max);
				}
				rule_fee = parseFloat(rule_fee);
				var final_fee = (get_fee - rule_fee).toFixed(2);
				var tips = "提现金额" + get_fee + "元, 手续费" + rule_fee + "元,实际到账" + final_fee + "元, 确定提现吗";
				setData({
					vue: that,
					url: 'manage/finance/getcash/getcash',
					confirm: tips,
					data: {
						fee: get_fee,
						channel: that.channel
					},
					success: function(res) {
						that.util.$toast(res, that.util.getUrl({path: '/pages/finance/index'}), 1000);
					},
					fail: (res) => {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#finance-getcash .getcash-num{
		border:1px solid #e7e7e7;
		border-radius:3px;
		font-size:14px;
		padding:1px 0 1px 6px;
		height: 34px;
		width: 100%;
		display: block;
	}
	#finance-getcash .getcash-rule p{
		color: #999;
		position: relative;
		padding-left: 10px;
		font-size: 14px;
		margin-top: 10px;
	}
	#finance-getcash .getcash-rule p:before{
		content: "";
		width: 5px;
		height: 5px;
		border-radius: 50%;
		position: absolute;
		left: 0;
		background: #ccc;
		top: 50%;
		margin-top: -2.5px;
	}
	#finance-getcash .getcash-type{
		font-size: 14px;
		padding: 0 15px;
		background-color: #fff;
	}
	#finance-getcash .van-cell__title{
		color: #999;
	}
</style>


